
body, html {
  line-height: 1;
  font-weight: 200;
  font-family: "宋体";
}
#app > .tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
}
#app > .tab > .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
#app > .tab > .tab-item > a {
  display: block;
  font-size: 14px;
  color: rgb(77,85,93);
}
#app > .tab > .tab-item > a.router-link-active{
  color: rgb(240,20,20);
}




